import React, { useState } from 'react';
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const ListingForm = (props) => {
    const [formData, setFormData] = useState({
        sale_type: 'For Sale',
        price: 'Ksh0+',
        bedrooms: '0+',
        property_type: 'House',
        bathrooms: '0+',
        sqft: '1000+',
        days_listed: '1 or less',
        has_photos: '1+',
        open_house: 'false',
        keywords: ''
    });

    const { sale_type, price, bedrooms, property_type, bathrooms, days_listed, has_photos, keywords } = formData;

    const [loading, setLoading] = useState(false);

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };

        setLoading(true);
        axios.post(`Ksh{process.env.REACT_APP_API_URL}/api/listings/search`, { sale_type, price, bedrooms, property_type, bathrooms, days_listed, has_photos, keywords }, config)
            .then(res => {
                setLoading(false);
                props.setListings(res.data);
                window.scrollTo(0, 0);
            })
            .catch(err => {
                setLoading(false);
                window.scrollTo(0, 0);
            })
    };

    return (
        <form onSubmit={e => onSubmit(e)}>
            <div class="row gx-lg-5">
                <div class="col-12">
                    <div class="input-box-one mb-35">
                        <div class="label" htmlFor='sale_type'>I’m looking to...</div>
                        <select class="nice-select fw-normal" style={{ border: 'none', width: '100%' }} name='property_type' onChange={e => onChange(e)} value={property_type}>
                            <option>Land</option>
                            <option>House</option>
                            <option>Apartment</option>
                            <option>Commerial Property</option>
                        </select>
                    </div>
                </div>
                <div class="col-12">
                    <div class="input-box-one mb-50">
                        <div class="label" htmlFor='sale_type'>Sale Type</div>
                        <select class="nice-select location fw-normal" style={{ border: 'none', width: '100%' }} name='sale_type' onChange={e => onChange(e)} value={sale_type}>
                            <option>For Sale</option>
                            <option>For Rent</option>

                        </select>
                    </div>
                </div>
                <div class="col-12">
                    <div class="input-box-one mb-35">
                        <div class="label" htmlFor='keywords'>Keyword</div>
                        <input placeholder="buy, home, apartment" class="type-input" name='keywords' type='text' onChange={e => onChange(e)} value={keywords} />
                    </div>
                </div>
                <div class="col-12">
                    <div class="input-box-one mb-50">
                        <div class="label" htmlFor='days_listed'>Day Listed</div>
                        <select class="nice-select location fw-normal" style={{ border: 'none', width: '100%' }} name='days_listed' onChange={e => onChange(e)} value={days_listed}>
                            <option>1 or Less</option>
                            <option>2 or Less</option>
                            <option>3 or Less</option>
                            <option>4 or Less</option>
                            <option>5 or Less</option>
                        </select>
                    </div>
                </div>
                <div class="col-12">
                    <div class="input-box-one mb-50">
                        <lable class="label" htmlFor='has_photos'>Property Photos</lable>
                        <select class="nice-select location fw-normal" style={{ border: 'none', width: '100%' }} name='has_photos' onChange={e => onChange(e)} value={has_photos}>
                            <option>1+</option>
                            <option>2+</option>
                            <option>3+</option>
                            <option>4+</option>
                            <option>5+</option>
                            <option>6+</option>
                            <option>7+</option>
                        </select>
                    </div>
                </div>
                <div class="col-12">
                    <div class="input-box-one mb-50">
                        <div class="label" htmlFor='price'>Min Price</div>
                        <select class="nice-select location fw-normal" style={{ border: 'none', width: '100%' }} name='price' onChange={e => onChange(e)} value={price}>
                            <option>Ksh0+</option>
                            <option>Ksh20,000+</option>
                            <option>Ksh200,000+</option>
                            <option>Ksh400,000+</option>
                            <option>Ksh600,000+</option>
                            <option>Ksh800,000+</option>
                            <option>Ksh1,000,000+</option>
                            <option>Ksh1,200,000+</option>
                            <option>Ksh1,500,000+</option>
                        </select>
                    </div>
                </div>
                <div class="col-sm-6">
                    <div class="input-box-one mb-40">
                        <div class="label" htmlFor='bedrooms'>Bedroom</div>
                        <select class="nice-select fw-normal" style={{ border: 'none', width: '100%' }} name='bedrooms' onChange={e => onChange(e)} value={bedrooms}>
                            <option>1+</option>
                            <option>2+</option>
                            <option>3+</option>
                        </select>
                    </div>
                </div>
                <div class="col-sm-6">
                    <div class="input-box-one mb-40">
                        <div class="label" htmlFor='bathrooms'>Baths</div>
                        <select class="nice-select fw-normal" style={{ border: 'none', width: '100%' }} name='bathrooms' onChange={e => onChange(e)} value={bathrooms}>
                            <option>1+</option>
                            <option>2+</option>
                            <option>3+</option>
                            <option>4+</option>
                            <option>5+</option>
                        </select>
                    </div>
                </div>
                <div class="col-12">
                    {loading ?
                        <div className='fw-500 text-uppercase tran3s apply-search w-100 mt-40 mb-25'>
                            <ThreeDots
                                type="Oval"
                                color="#ffffff"
                                height={50}
                                width={50}
                                adius="9"
                                ariaLabel="three-dots-loading"
                            />
                        </div> :
                        <button class="fw-500 text-uppercase tran3s apply-search w-100 mt-40 mb-25">
                            <i class="fa-light fa-magnifying-glass"></i>
                            <span>Search</span>
                        </button>
                    }
                </div>
                <div class="col-12">
                    <div class="d-flex justify-content-between form-widget">
                        <Link href="#" class="tran3s">
                            <i class="fa-regular fa-arrows-rotate"></i>
                            <span>Reset Filter</span>
                        </Link>
                        {loading ?
                            <div className='tran3s'>
                                <ThreeDots
                                    type="Oval"
                                    color="#ffffff"
                                    height={50}
                                    width={50}
                                    adius="9"
                                    ariaLabel="three-dots-loading"
                                />
                            </div> :
                            <Link class="tran3s">
                                <i class="fa-regular fa-star"></i>
                                <span>Save Search</span>
                            </Link>
                        }
                      </div>
                </div>
            </div>
        </form>
    );
};

ListingForm.propTypes = {
    setListings: PropTypes.func.isRequired
};

export default ListingForm;
