import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import Fancybox from '../components/Fancybox';

const ProjectDetails = () => {
    const [project, setProject] = useState(null);
    const { id } = useParams();

    useEffect(() => {
        const fetchProject = async () => {
            try {
                const config = {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                };

                const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/projects/${id}`, config);

                setProject(res.data);
            }
            catch (err) {
                console.log('API request failed:', err);
            }
        };

        fetchProject();
    }, [id]);


    if (!project) {
        return <div>Loading...</div>;
    }
    return (
        <div>
            <Helmet>
                <title>Pevik Holdings - {project.title}</title>
            </Helmet>
            <div class="main-page-wrapper">
                <div class="inner-banner-two inner-banner z-1 pt-160 lg-pt-130 pb-160 xl-pb-120 md-pb-80 position-relative" style={{ backgroundImage: 'url(./assets/images/house.jpg)', }}>
                    <div class="container">
                        <div class="row">
                            <div class="col-lg-6">
                                <h3 class="mb-35 xl-mb-20 pt-15">Single Project</h3>
                                <ul class="theme-breadcrumb style-none d-inline-flex align-items-center justify-content-center position-relative z-1 bottom-line">
                                    <li><Link to='/'>Home</Link></li>
                                    <li>/</li>
                                    <li><Link to='/projects'>Project</Link></li>
                                    <li>/</li>
                                    <li>{project.title}</li>
                                </ul>
                            </div>
                            <div class="col-lg-6">
                                <p class="sub-heading">Over 745,000 listings, apartments, lots and  plots available now!</p>
                            </div>
                        </div>
                    </div>
                    <img src="assets/images/assets/ils_07.svg" alt="" class="lazy-img shapes w-100 illustration" />
                </div>
            </div>
            <div class="project-details-one mt-150 xl-mt-100 mb-170 xl-mb-100">
                <div class="container">
                    <div class="row gx-xxl-5">
                        <div class="col-lg-6 order-lg-first">
                            <figure class="image-wrapper">
                                <img src={project.photo_main} alt="" class="lazy-img w-100" width={640} height={420} />
                            </figure>
                            <figure class="image-wrapper">
                                <img src={project.photo_1} alt="" class="lazy-img w-100" width={640} height={420} />
                            </figure>
                            <figure class="image-wrapper">
                                <img src={project.photo_2} alt="" class="lazy-img w-100" width={640} height={420} />
                            </figure>
                        </div>
                        <div class="col-lg-6">
                            <div class="details-text ps-xxl-5 md-mt-40">
                                <div class="tag fw-500 text-uppercase">{project.project_type}</div>
                                <h3>{project.title}</h3>
                                <p class="fs-24 pt-45 xl-pt-30 pb-45 xl-pb-30">Your leading real estate advocate, transforming houses into dreams. </p>
                                <h4 class="mb-40">Project Details</h4>
                                <div class="project-info-outline">
                                    <div class="main-bg">
                                        <ul class="style-none">
                                            <li class="position-relative z-1">
                                                <div class="num fw-light">01</div>
                                                <i class="fa-solid fa-calendar-days icon"></i>
                                                <strong>Date</strong>
                                                <span>{project.list_date}</span>
                                            </li>
                                            <li class="position-relative z-1">
                                                <div class="num fw-light">02</div>
                                                <i class="fa-solid fa-ballot-check icon"></i>
                                                <strong>Project Type</strong>
                                                <span>{project.project_type}</span>
                                            </li>
                                            <li class="position-relative z-1">
                                                <div class="num fw-light">03</div>
                                                <i class="fa-sharp fa-solid fa-building icon"></i>
                                                <strong>No Of Units</strong>
                                                <span>{project.units}</span>
                                            </li>
                                            <li class="position-relative z-1">
                                                <div class="num fw-light">04</div>
                                                <i class="fa-sharp fa-solid fa-building icon"></i>
                                                <strong>Price</strong>
                                                <span>{project.price}&nbsp;<i class="fa-solid fa-arrows-from-line fa-rotate-90"></i>&nbsp;{project.price}</span>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                

                                <div class="page-pagination mt-45 pt-50">
                                    <div class="d-flex align-items-center justify-content-between">
                                        <div class="d-flex align-items-center page-title position-relative">
                                            <img src="images/project/img_28.jpg" alt="" class="lazy-img img" />
                                            <div className="ps-4">
                                                <span>Call Versailles garden</span>
                                                <Link to='/'>
                                                    <h5 className="m0"><i class="fa-solid fa-arrow-turn-down-right"></i>&nbsp;&nbsp;0706641895</h5>
                                                </Link>
                                            </div>
                                        </div>
                                        <Link href="#" class="btn-four rounded-circle inverse"><i class="fa-solid fa-phone-volume icon"></i></Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="project-details-one mt-150 xl-mt-100 mb-170 xl-mb-100">
                <div class="container">
                    <div class="row gx-xxl-5">
                    <p class="fs-20 pt-50 pb-35"><h4 class="mb-30">Overview</h4><br/>{project.description}</p>
                        <div class="listing-details-one theme-details-one bg-green-one lg-pt-150 pb-80 xl-pb-120">
                            <div class="bg-white shadow4 border-20">
                                <div class="property-video-tour border-top p-40">
                                    <h4 class="mb-30">Video Tour</h4>
                                    <div class="position-relative border-15 image-bg overflow-hidden z-1">
                                        <img src={project.photo_4} alt=""
                                            class="lazy-img w-100" />
                                        <Fancybox
                                            options={{
                                                Carousel: {
                                                    infinite: false,
                                                },
                                            }}
                                        >
                                            <a class="video-icon tran3s rounded-circle d-flex align-items-center justify-content-center"
                                                data-fancybox href={project.video_url} data-caption={project.title}>
                                                <i class="fa-thin fa-play"></i>
                                            </a>
                                        </Fancybox>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ProjectDetails;