import React, { useState } from 'react';
import axios from 'axios';
import { ThreeDots } from 'react-loader-spinner';
import PropTypes from 'prop-types';

const ListingForm = (props) => {
    const [formData, setFormData] = useState({
        sale_type: 'For Sale',
        price: 'Ksh0+',
        bedrooms: '0+',
        property_type: 'House',
        bathrooms: '0+',
        sqft: '1000+',
        days_listed: '1 or less',
        has_photos: '1+',
        open_house: 'false',
        keywords: ''
    });

    const { sale_type, city, price, bedrooms, property_type, bathrooms, sqft, days_listed, has_photos, open_house, keywords } = formData;

    const [loading, setLoading] = useState(false);

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };

        setLoading(true);
        axios.post(`Ksh{process.env.REACT_APP_API_URL}/api/listings/search`, { sale_type, city, price, bedrooms, property_type, bathrooms, sqft, days_listed, has_photos, open_house, keywords }, config)
        .then(res => {
            setLoading(false);
            props.setListings(res.data);
            window.scrollTo(0, 0);
        })
        .catch(err => {
            setLoading(false);
            window.scrollTo(0, 0);
        })
    };

    return (
      <div class="modal fade" id="advanceFilterModal" tabindex="-1" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
          <div class="container">
              <div class="row">
                  <div class="col-xl-8 m-auto">
                      <div class="modal-content">
                          <button type="button" class="btn-close ms-auto mt-20 me-4" data-bs-dismiss="modal" aria-label="Close"><i class="fa-regular fa-xmark"></i></button>
                          <div class="advance-search-panel">
                              <div class="main-bg border-0">
                                  <form onSubmit={e => onSubmit(e)}>
                                      <div class="row gx-lg-5">
                                          <div class="col-md-6">
                                              <div class="input-box-one mb-35">
                                                  <div class="label">I’m looking to...</div>
                                                  <select class="nice-select fw-normal" name='property_type' style={{ border: 'none'}} onChange={e => onChange(e)} value={property_type}>
                                                      <option value="3">Land</option>
                                                      <option value="4">House</option>
                                                      <option value="1">Apartments</option>
                                                      <option value="2">Commercial</option>
                                                  </select>
                                              </div>
                                          </div>
                                          <div class="col-md-6">
                                              <div class="input-box-one mb-35">
                                                  <div class="label">Location</div>
                                                  <select class="nice-select location fw-normal" style={{ border: 'none'}} name='city' onChange={e => onChange(e)} value={sale_type}>
                                                      <option>For Sale</option>
                                                      <option>For Rent</option>
                                                      
                                                  </select>
                                              </div>
                                          </div>
                                          <div class="col-md-6">
                                              <div class="input-box-one mb-35">
                                                  <div class="label">Minimum Price</div>
                                                  <select className='nice-select fw-normal' style={{ border: 'none'}} name='price' onChange={e => onChange(e)} value={price}>
                                                      <option>Ksh0+</option>
                                                      <option>Ksh20,000</option>
                                                      <option>Ksh200,000+</option>
                                                      <option>Ksh400,000+</option>
                                                      <option>Ksh600,000+</option>
                                                      <option>Ksh800,000+</option>
                                                      <option>Ksh1,000,000+</option>
                                                      <option>Ksh1,200,000+</option>
                                                      <option>Ksh1,500,000+</option>
                                                      <option>Any</option>
                                                  </select>
                                              </div>
                                          </div>
                                          <div class="col-md-6">
                                              <div class="input-box-one mb-35">
                                                  <div class="label">Day Listed</div>
                                                  <select className='nice-select fw-normal' style={{ border: 'none'}} name='days_listed' onChange={e => onChange(e)} value={days_listed}>
                                                      <option>1 of less</option>
                                                      <option>2 of less</option>
                                                      <option>5 of less</option>
                                                      <option>10 of less</option>
                                                      <option>20 of less</option>
                                                      <option>Any</option>
                                                  </select>
                                              </div>
                                          </div>
                                          
                                          <div class="col-lg-6">
                                              <div class="input-box-one mb-40">
                                                  <div class="label">Bedroom</div>
                                                  <select class="nice-select fw-normal" style={{ border: 'none'}} name='bedrooms' onChange={e => onChange(e)} value={bedrooms}>
                                                      <option>0+</option>
                                                      <option>1+</option>
                                                      <option>2+</option>
                                                      <option>3+</option>
                                                      <option>5+</option>
                                                  </select>
                                              </div>
                                          </div>
                                          <div class="col-lg-6">
                                              <div class="input-box-one mb-40">
                                                  <div class="label">Bath</div>
                                                  <select class="nice-select fw-normal" style={{ border: 'none'}} name='bathrooms' onChange={e => onChange(e)} value={bathrooms}>
                                                      <option>0+</option>
                                                      <option>1+</option>
                                                      <option>2+</option>
                                                      <option>3+</option>
                                                  </select>
                                              </div>
                                          </div>
                                          {/*<div class="col-12">
                                              <h6 class="block-title fw-bold mb-30">Amenities</h6>
                                              <ul class="style-none d-flex flex-wrap justify-content-between filter-input">
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="01"/>
                                                      <label>A/C & Heating</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="02"/>
                                                      <label>Garages</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="03"/>
                                                      <label>Garden</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="04"/>
                                                      <label>Disabled Access</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="05"/>
                                                      <label>Swimming Pool</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="06"/>
                                                      <label>Parking</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="07"/>
                                                      <label>Wifi</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="08"/>
                                                      <label>Pet Friendly</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="09"/>
                                                      <label>Ceiling Height</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="10"/>
                                                      <label>Fireplace</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="11"/>
                                                      <label>Play Ground</label>
                                                  </li>
                                                  <li>
                                                      <input type="checkbox" name="Amenities" value="12"/>
                                                      <label>Elevator</label>
                                                  </li>
                                              </ul>
                                          </div>*/}
                                          <div class="col-md-6">
                                              <div class="input-box-one border-left">
                                                  <div class="label">Keyword</div>
                                                  <input name='keywords' type='text' onChange={e => onChange(e)} value={keywords} placeholder="Fiber,Air Con.." class="type-input"/>
                                              </div>
                                          </div>
                                          <div class="col-md-6">
                                            <div className='input-box-one border-left'>
                                                <div class="label">Property Photos</div>
                                                  <select className='nice-select fw-normal' style={{ border: 'none'}} name='has_photos' onChange={e => onChange(e)} value={has_photos}>
                                                    <option>1+</option>
                                                    <option>2+</option>
                                                    <option>3+</option>
                                                    <option>4+</option>
                                                    <option>5+</option>
                                                    <option>6+</option>
                                                    <option>7+</option>
                                                  </select>
                                              </div>
                                          </div>
                                          <div class="col-12">
                                          {loading ?
                                              <div className='fw-500 text-uppercase tran3s apply-search w-100 mt-40 mb-25'>
                                                  <ThreeDots
                                                      type="Oval"
                                                      color="#ffffff"
                                                      height={50}
                                                      width={50}
                                                      adius="9"
                                                      ariaLabel="three-dots-loading"
                                                  />
                                              </div> : 
                                              <button class="fw-500 text-uppercase tran3s apply-search w-100 mt-40 mb-25">
                                              <i class="fa-light fa-magnifying-glass"></i>
                                              <span>Search</span>
                                          </button>
                                          }    
                                          </div>
                                          <div class="col-12">
                                          <center>
                                          {loading ?
                                              <div className='d-flex justify-content-between form-widget'>
                                                  <ThreeDots
                                                      type="Oval"
                                                      color="#ffffff"
                                                      height={25}
                                                      width={25}
                                                      adius="9"
                                                      ariaLabel="three-dots-loading"
                                                  />
                                              </div> : 
                                              <button className='tran3s'><i class="fa-regular fa-star"></i><span>Save Search</span></button>
                                          }
                                          </center> 
                                          </div>
                                      </div>
                                  </form>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
    );
};

ListingForm.propTypes = {
    setListings: PropTypes.func.isRequired
};

export default ListingForm;
