import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Pagination from '../components/Pagination';
import ProjectsComponent from '../components/ProjectsComponent';

const Projects = () => {
    const [projects, setProjects] = useState([]);
    const [count, setCount] = useState(1);
    const [previous, setPrevious] = useState('');
    const [next, setNext] = useState('');
    const [active, setActive] = useState('');
    const [activeFilter, setActiveFilter] = useState('*');
    const [filteredProjects, setFilteredProjects] = useState([]);

    useEffect(() => {
        window.scrollTo(0, 0);

        const fetchData = async () => {
            try {
                const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/projects/?page=1`);

                setProjects(res.data.results);
                setCount(res.data.count);
                setPrevious(res.data.previous);
                setNext(res.data.next);
            }
            catch (err) {
                console.log('API request failed:', err);
            }
        }

        fetchData();
    }, []);

    const visitPage = (page) => {
        axios.get(`${process.env.REACT_APP_API_URL}/api/projects/?page=${page}`)
            .then(res => {
                setProjects(res.data.results);
                setPrevious(res.data.previous);
                setNext(res.data.next);
                setActive(page);
            })
            .catch(err => {

            });
    };

    const previous_number = () => {
        axios.get(previous)
            .then(res => {
                setProjects(res.data.results);
                setPrevious(res.data.previous);
                setNext(res.data.next);
                if (previous)
                    setActive(active - 1);
            })
            .catch(err => {

            });
    };

    const next_number = () => {
        axios.get(next)
            .then(res => {
                setProjects(res.data.results);
                setPrevious(res.data.previous);
                setNext(res.data.next);
                if (next)
                    setActive(active + 1);
            })
            .catch(err => {

            });
    };

    useEffect(() => {
        setFilteredProjects(projects);
    }, [projects]);

    const handleFilterChange = (filterValue) => {
        setActiveFilter(filterValue);
        if (filterValue === '*') {
            setFilteredProjects(projects);
        } else {
            const filtered = projects.filter(project => project.project_type === filterValue);
            setFilteredProjects(filtered);
        }
    };

    return (
        <div class="main-page-wrapper">
            <div class="inner-banner-one inner-banner bg-pink text-center z-1 pt-160 lg-pt-130 pb-160 xl-pb-120 md-pb-80 position-relative">
                <div class="container">
                    <h3 class="mb-35 xl-mb-20 pt-15">Our Projects</h3>

                </div>
                <img src="assets/images/assets/ils_07.svg" alt="" class="lazy-img shapes w-100 illustration" />
            </div>
            <div class="project-section-one mt-150 xl-mt-100">
                <div class="container">
                    <div class="filter-nav-one style-two">
                        <ul className="style-none d-flex justify-content-center flex-wrap isotop-menu-wrapper">
                            <li onClick={() => handleFilterChange('*')} className={activeFilter === '*' ? 'is-checked' : ''}>All</li>
                            <li onClick={() => handleFilterChange('Development Project')} className={activeFilter === 'Development Project' ? 'is-checked' : ''}>Development Plan</li>
                            <li onClick={() => handleFilterChange('Off Plan Project')} className={activeFilter === 'Off Plan Project' ? 'is-checked' : ''}>Off Plan</li>
                            <li onClick={() => handleFilterChange('Ready Project')} className={activeFilter === 'Ready Project' ? 'is-checked' : ''}>Ready Plan</li>
                        </ul>
                    </div>
                    <div id="isotop-gallery-wrapper" className="grid-2column pt-10">
                        <div className="grid-sizer"></div>
                        <ProjectsComponent projects={filteredProjects} />
                    </div>
                    <Pagination
                        itemsPerPage={3}
                        count={count}
                        visitPage={visitPage}
                        previous={previous_number}
                        next={next_number}
                        active={active}
                        setActive={setActive}
                    />
                </div>
            </div>
        </div>
    );
};

export default Projects;