import React, { useState } from 'react';
import axios from 'axios';
import { Circles } from 'react-loader-spinner';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const ListingFormHome = (props) => {
    const [formData, setFormData] = useState({
        sale_type: 'For Sale',
        price: 'Ksh0+',
        bedrooms: '0+',
        property_type: 'Land',
        bathrooms: '0+',
        sqft: '1000+',
        days_listed: '1 or less',
        has_photos: '1+',
        open_house: 'false',
        keywords: ''
    });

    const { sale_type, days_listed, price, keywords } = formData;

    const [loading, setLoading] = useState(false);

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };

        setLoading(true);
        axios.post(`${process.env.REACT_APP_API_URL}/api/listings/search`, { sale_type, days_listed, price, keywords }, config)
        .then(res => {
            setLoading(false);
            props.setListings(res.data);
            window.scrollTo(0, 0);
        })
        .catch(err => {
            setLoading(false);
            window.scrollTo(0, 0);
        })
    };

    return (
        <form className='listingform' onSubmit={e => onSubmit(e)}>
            <div class="row gx-0 align-items-center">
                <div class="col-xl-2 col-md-6">
                    <div class="input-box-one border-left">
                        <div class="label">I’m looking to...</div>
                        <select className="nice-select fw-normal" name='sale_type' style={{ border: 'none'}} onChange={e => onChange(e)} value={sale_type}>
                            <option value="1">For Seal</option>
                            <option value="2">For Rent</option>
                        </select>
                    </div>
                </div>
                <div class="col-xl-3 col-md-6">
                    <div class="input-box-one border-left">
                        <div class="label">Day Listed</div>
                        <select class="nice-select fw-normal" name='days_listed' style={{ border: 'none'}} onChange={e => onChange(e)} value={days_listed}>
                            <option value="1">1 0r Less</option>
                            <option value="2">2 or Less</option>
                            <option value="3">3 or Less</option>
                            <option value="4">4 or Less</option>
                        </select>
                    </div>
                </div>
                <div class="col-xl-3 col-md-6">
                    <div class="input-box-one border-left">
                        <div class="label">Keyword</div>
                        <input name='keywords' type='text' onChange={e => onChange(e)} value={keywords} placeholder="Fiber,Air Con.." class="type-input"/>
                    </div>
                </div>
                <div class="col-xxl-3 col-xl-3">
                    <div class="input-box-one lg-mt-10">
                        <div class="d-flex align-items-center justify-content-center">
                            <Link href="#" data-bs-toggle="modal" data-bs-target="#advanceFilterModal" class="search-modal-btn tran3s text-uppercase fw-500 d-inline-flex align-items-center me-3">
                                <span class="d-xl-none d-xxl-block">Advance Search</span>
                                    <i class="fa-light fa-sliders-up"></i>
                            </Link>
                        </div>
                    </div>
                </div>
                <div class="col-xl-1">
                    {loading ?
                    <div className='input-box-one lg-mt-10'>
                        <Circles
                                type="Oval"
                                color="#424242"
                                height={50}
                                width={50}
                            />
                    </div>:
                        <button class="fw-500 text-uppercase tran3s search-btn-two"><i class="fa-light fa-magnifying-glass"></i></button>
                     }
                </div>
            </div>
        </form>
    );
};

ListingFormHome.propTypes = {
    setListings: PropTypes.func.isRequired
};

export default ListingFormHome;
